.headerWrap {
    display: flex;
    flex-direction: row;
    flex: 1;
    height: 88px;
}

.Logo {
    margin: auto 0 auto 165px;
    /*top right bottom left*/
}

.Search {
    display: flex;
    flex-direction: row;
    height: 40px;
    width: 350px;
    margin: auto 0 auto 32px;
    border-radius: 100px;
    background-color: white;
}

.icoSearch {
    margin: auto 12px auto 16px;
}

.Search input {
    height: 40px;
    width: 300px;
    border-width: 0;
    padding: 0;
    background-color: unset;
    outline: none;

}

.UserDiv {
    display: flex;
    flex-direction: row;
    margin: auto 165px auto 496px;
}

.SpanNotification {
    margin: auto;
}

.notification {
    height: 24px;
    width: 24px;
}

.avatar {
    margin: auto 0 auto 26px;
}

.user {
    height: 40px;
    width: 40px;
}

