.NavbarWrap {
    display: flex;
    flex-direction: row;
    height: 48px;
    margin: auto;
}

.NavbarWrap div:nth-child(n) {
    margin: auto 33px auto 0;
    margin-right: 33px;
    text-align: center;
    display: inline-block;
}

.NavbarWrap div:first-child {
    margin-left: 165px;
    text-align: center;
    display: inline-block;
}



.NavbarWrap div:nth-child(7) {
    height: 24px;
    width:1px;
    background-color: #b1b1b1;
}

a {
    text-decoration: none;
}

.item a {
    color: black;
}

a.activeLink {
    color: #336CFB;
}
